.sub-container {
  width: 90%;
  margin: auto;
  text-align: center;
  margin-top: 30px;
}

.main-container {
  display: flex;
  gap: 30px;
  margin-top: 25px;
  border-radius: 30px;
  padding: 3%;
 box-shadow: rgba(82, 81, 81, 0.35) 0px 5px 15px;
}

.project-card {
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border: 1px solid grey;
  padding: 2%;
  border-radius: 15px;
}
.project-card > p   {
 padding: 2%;
}

