.about {
  width: 90%;
  margin: auto;
  margin-top: 3%;
}
.about-containt {
    display: flex;
    gap:5%;
  margin-top: 30px;
  box-shadow: rgba(112, 109, 109, 0.35) 0px 5px 15px;
  border-radius: 30px;
  padding: 5%;
}
.profile-image{
    width:100%;
    margin:auto;
    
}

.left{
    
     margin: auto;
     border: 1px solid black;
     border-radius: 15px;
}
.left>img{
   border-radius: 15px; 
}
.right{
    margin: auto;
}

.heading{
    margin-top: 20px;
    text-align: center;
}

.btn-resume{
    display: flex;
    justify-content: center;
    align-items: center;
    margin:auto;
    margin-top: 25px;
    margin-bottom: 15px;
}
.heading .lines{
    position:relative;
}
.heading .line{
    padding: 2%;
}

.heading .lines::before{
    content: "";
    position: absolute;
    bottom: -10px;
    left:50%;
    width:170px;
    height:4px;
    margin-top: 15px;
    background: #4FD1C5;
    transform: translateX(-50%) translateY(20%) ;
}

